import { gsap } from "gsap";
import React, { useLayoutEffect, useRef } from "react";
import styles from '../../../App.module.scss';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import loadingVideo from "../../../assets/video/main_video2.mp4"


gsap.registerPlugin(ScrollTrigger);
function Section01() {
  const mainBox = useRef();
  const txtRef01_1= useRef();
  const txtRef02_1= useRef();
  const txtRef03_1= useRef();
  const txtRef01_2= useRef();
  const txtRef02_2= useRef();
  const txtRef03_2= useRef();
  const txtLeftRef= useRef();
  const txtRightRef= useRef();
  const parents = useRef();

  const videoRef= useRef();
  const videoBoxRef = useRef();
  const videoBoxLineRef = useRef([]);
  const setPlayBackRate = () => {
    videoRef.current.playbackRate = 0.5;
  };
  useLayoutEffect(() => {
    let ctx = gsap.context(() => {
      var tl = gsap.timeline();

      tl.from(txtRef01_1.current,{
        yPercent: 100,
      }, 0.2);
      tl.to(txtRef01_1.current,{
        yPercent: 0,
      }, 0.2);

      tl.from(txtRef02_1.current,{
        yPercent: 100,
      }, 0.5);
      tl.to(txtRef02_1.current,{
        yPercent: 0,
      }, 0.5);

      tl.from(txtRef03_1.current,{
        yPercent: 100,
      }, 0.8);
      tl.to(txtRef03_1.current,{
        yPercent: 0,
      }, 0.8);

      
      // tl.from(videoBoxRef.current,{
      //   xPercent: 0,
      // }, 1.0);
      // tl.to(videoBoxRef.current,{
      //   xPercent: 100,
      //   duration: 1,
      //   ease: 'ease.in',
      // }, 1.0);

      
      tl.to(videoBoxLineRef.current, {
        height: "100%",
      }, 1.0);
      tl.to(videoBoxLineRef.current,{ 
        height: "0%",
        duration: 1,
        ease: 'ease.in',
        stagger: 0.15,
      }, 1.0);
      
      tl.from(txtRef01_2.current,{
        opacity: 0,
      }, 2.0);
      tl.to(txtRef01_2.current,{
        opacity: 1,
      }, 2.0);

      tl.from(txtRef02_2.current,{
        opacity: 0,
      }, 2.3);
      tl.to(txtRef02_2.current,{
        opacity: 1,
      }, 2.3);

      tl.from(txtRef03_2.current,{
        opacity: 0,
      }, 2.6);
      tl.to(txtRef03_2.current,{
        opacity: 1,
      }, 2.6);

      
      gsap.to(mainBox.current, {
        yPercent: 0,
        opacity: 0,
        scrollTrigger: {
          trigger: mainBox.current,
          start: "20% top",
          pin: false,
          scrub: true,
        }
      });

      
      gsap.to(txtRightRef.current, {
        yPercent: -20,
        scrollTrigger: {
          trigger: mainBox.current,
          start: "top top",
          pin: false,
          scrub: true,
        }
      });
      
      gsap.to(txtLeftRef.current, {
        xPercent: -20,
        scrollTrigger: {
          trigger: mainBox.current,
          start: "20% top",
          pin: false,
          scrub: true,
        }
      });
      
      
      
      
    }, parents);
    
    return () => ctx.revert();
  }, []);

  return (
    <div className={styles.main_section01} ref={parents}>
      <div className={styles.main_section01_wrap} ref={mainBox}>
        <div className={styles.main_section_video}>
          <div className={styles.main_video_box} ref={videoBoxRef}>
            <div ref={elem => (videoBoxLineRef.current[0] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[1] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[2] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[3] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[4] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[5] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[6] = elem)}></div>
            <div ref={elem => (videoBoxLineRef.current[7] = elem)}></div>
          </div>
          <video muted autoPlay loop ref={videoRef} onCanPlay={() => setPlayBackRate()} >
            <source src={loadingVideo} type="video/mp4" />
          </video>
        </div>
        <div className={styles.main_text_right} ref={txtRightRef}>
          <div className={styles.main_text_wrap}>
            <div className={styles.main_section_text01} ref={txtRef01_1}>
              <span>PULBLISHER</span>
            </div>
          </div>
          <div className={styles.main_text_wrap}>
            <div className={styles.main_section_text01} ref={txtRef02_1}>
              <span>SANGHO'S</span>
            </div>
          </div>
          <div className={styles.main_text_wrap}>
            <div className={styles.main_section_text01} ref={txtRef03_1}>
              <span>PORTFOLIO</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Section01;
