import { gsap } from "gsap";
import React, { useLayoutEffect, useRef } from "react";
import styles from '../../../App.module.scss';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Link } from "react-router";
import Lottie from "lottie-react";
import lottie_mouse from "../../../assets/images/lottie_mouse.json";


gsap.registerPlugin(ScrollTrigger);
function Section04() {
  const parents = useRef();
  const sec04Hypen = useRef();
  const sec04Scroll = useRef();
  const sec04Title = useRef([]);
  const sec04itemBack = useRef([]);

  useLayoutEffect(() => {
    let ctx = gsap.context(() => {

      gsap.set(sec04Hypen.current, {
        height: 0,
      });
      gsap.to(sec04Hypen.current, {
        height: 250,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: true,
        }
      });
      gsap.set(sec04Scroll.current, {
        yPercent: 100,
        opacity: 0,
      });
      gsap.to(sec04Scroll.current, {
        yPercent: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: 2,
        }
      });

      
      gsap.set(sec04Title.current, {scale: 1, y: 100, opacity: 0,});
      gsap.to(sec04Title.current, {
        scale: 1,
        opacity: 1,
        y: 0,
        duration: 1,
        ease: "bounce.out",
        stagger: 0.05,
        scrollTrigger: {
          trigger: parents.current,
          start: "20% bottom",
          //end: "top bottom",
          toggleActions: "play none none reverse",
        }
      });


      sec04itemBack.current.forEach((el, index) => {
        gsap.fromTo(el, {
          width: "100%",
        }, {
        duration: 0.5, 
        width: "0%",
        ease: 'none',
        scrollTrigger: {
          id: sec04itemBack[index],
          trigger: el,
          start: "40% bottom",
          toggleActions: 'play none none reverse'
        }
      });
    });

    }, parents);
    
    return () => ctx.revert();
  }, []);

  return (
    <div className={styles.main_section04} ref={parents}>
      <div className={styles.section_hypen} ref={sec04Hypen}></div>
      <div className={styles.section_scroll_wrap} ref={sec04Scroll}>
        <div className={styles.section_scroll}>
          <Lottie className={styles.section_scroll_img} animationData={lottie_mouse} loop play speed={5} />
        </div>
      </div>
      <div className={styles.section_title}>
        <span ref={elem => (sec04Title.current[0] = elem)}>M</span>
        <span ref={elem => (sec04Title.current[1] = elem)}>Y</span>
        <span ref={elem => (sec04Title.current[2] = elem)}>-</span>
        <span ref={elem => (sec04Title.current[3] = elem)}>W</span>
        <span ref={elem => (sec04Title.current[4] = elem)}>O</span>
        <span ref={elem => (sec04Title.current[5] = elem)}>R</span>
        <span ref={elem => (sec04Title.current[6] = elem)}>K</span>
        <span ref={elem => (sec04Title.current[7] = elem)}>S</span>
      </div>
      <div className={styles.main_section04_portWrap}>
        <ul>
          <li>
            <div className={styles.main_section04_itemBack} ref={elem => (sec04itemBack.current[0] = elem)}></div>
            <div className={styles.main_section04_itemBox}>
              <div className={styles.main_section04_itemLeft}>
                <div className={styles.main_section04_item_num}>
                  <span className={styles.main_section04_item_num_text}>01</span>
                </div>
              </div>
              <div className={styles.main_section04_itemCenter}>
                <div className={styles.main_section04_item_title}>
                  <span className={styles.main_section04_item_title_text}>서울호서예술전문학교</span>
                </div>
                <div className={styles.main_section04_item_ment}>
                  <span className={styles.main_section04_item_ment_text}>
                    메인페이지부터 서브페이지까지 대부분의 페이지의 내용이 길어서 사이트를 보며 지루하지않도록 간결한 레이아웃과 스크롤시 애니메이션에 중점을 두고 작업했습니다. 
                  </span>
                </div>
                <div className={styles.main_section04_item_tag}>
                  <span className={styles.main_section04_item_tag_text}>HTML</span>
                  <span className={styles.main_section04_item_tag_text}>CSS</span>
                  <span className={styles.main_section04_item_tag_text}>JAVASCRIPT</span>
                  <span className={styles.main_section04_item_tag_text}>JQUERY</span>
                  <span className={styles.main_section04_item_tag_text}>SWIPER.JS</span>
                </div>
              </div>
              <div className={styles.main_section04_itemRight}>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    GO SITE
                  </Link>
                </div>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    CODE REVIEW
                  </Link>
                </div>
              </div>
            </div>
          </li>
          <li>
            <div className={styles.main_section04_itemBack} ref={elem => (sec04itemBack.current[1] = elem)}></div>
            <div className={styles.main_section04_itemBox}>
              <div className={styles.main_section04_itemLeft}>
                <div className={styles.main_section04_item_num}>
                  <span className={styles.main_section04_item_num_text}>02</span>
                </div>
              </div>
              <div className={styles.main_section04_itemCenter}>
                <div className={styles.main_section04_item_title}>
                  <span className={styles.main_section04_item_title_text}>KU창업포탈</span>
                </div>
                <div className={styles.main_section04_item_ment}>
                  <span className={styles.main_section04_item_ment_text}>
                    스케쥴이나 데이터 관리를 위한 VANILLA JS의 사용과 적절한 애니메이션 사용에 중점을 두고 작업했습니다.
                    
                  </span>
                </div>
                <div className={styles.main_section04_item_tag}>
                  <span className={styles.main_section04_item_tag_text}>HTML</span>
                  <span className={styles.main_section04_item_tag_text}>CSS</span>
                  <span className={styles.main_section04_item_tag_text}>JAVASCRIPT</span>
                  <span className={styles.main_section04_item_tag_text}>SWIPER.JS</span>
                  <span className={styles.main_section04_item_tag_text}>AOS.JS</span>
                </div>
              </div>
              <div className={styles.main_section04_itemRight}>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    GO SITE
                  </Link>
                </div>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    CODE REVIEW
                  </Link>
                </div>
              </div>
            </div>
          </li>
          <li>
            <div className={styles.main_section04_itemBack} ref={elem => (sec04itemBack.current[2] = elem)}></div>
            <div className={styles.main_section04_itemBox}>
              <div className={styles.main_section04_itemLeft}>
                <div className={styles.main_section04_item_num}>
                  <span className={styles.main_section04_item_num_text}>03</span>
                </div>
              </div>
              <div className={styles.main_section04_itemCenter}>
                <div className={styles.main_section04_item_title}>
                  <span className={styles.main_section04_item_title_text}>두타몰</span>
                </div>
                <div className={styles.main_section04_item_ment}>
                  <span className={styles.main_section04_item_ment_text}>
                    기존에 JSP언어로 개발되어있는 두타몰 웹과 앱의 수정과 신규 페이지나 메뉴의 추가 업무를 담당했었습니다.
                  </span>
                </div>
                <div className={styles.main_section04_item_tag}>
                  <span className={styles.main_section04_item_tag_text}>HTML</span>
                  <span className={styles.main_section04_item_tag_text}>CSS</span>
                  <span className={styles.main_section04_item_tag_text}>JAVASCRIPT</span>
                  <span className={styles.main_section04_item_tag_text}>SWIPER.JS</span>
                  <span className={styles.main_section04_item_tag_text}>JSP (수정)</span>
                </div>
              </div>
              <div className={styles.main_section04_itemRight}>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    GO SITE
                  </Link>
                </div>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    CODE REVIEW
                  </Link>
                </div>
              </div>
            </div>
          </li>
          <li>
            <div className={styles.main_section04_itemBack} ref={elem => (sec04itemBack.current[3] = elem)}></div>
            <div className={styles.main_section04_itemBox}>
              <div className={styles.main_section04_itemLeft}>
                <div className={styles.main_section04_item_num}>
                  <span className={styles.main_section04_item_num_text}>04</span>
                </div>
              </div>
              <div className={styles.main_section04_itemCenter}>
                <div className={styles.main_section04_item_title}>
                  <span className={styles.main_section04_item_title_text}>롯데에너지머티리얼즈</span>
                </div>
                <div className={styles.main_section04_item_ment}>
                  <span className={styles.main_section04_item_ment_text}>
                    메인페이지에서의 역동적인 스크롤 애니메이션과 서브페이지의 메뉴와 컨텐츠들의 인터랙티브 애니메이션에 중점을 두었습니다.
                  </span>
                </div>
                <div className={styles.main_section04_item_tag}>
                  <span className={styles.main_section04_item_tag_text}>HTML</span>
                  <span className={styles.main_section04_item_tag_text}>CSS</span>
                  <span className={styles.main_section04_item_tag_text}>JAVASCRIPT</span>
                  <span className={styles.main_section04_item_tag_text}>GSAP</span>
                  <span className={styles.main_section04_item_tag_text}>FULLPAGE.JS</span>
                  <span className={styles.main_section04_item_tag_text}>SWIPER.JS</span>
                </div>
              </div>
              <div className={styles.main_section04_itemRight}>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    GO SITE
                  </Link>
                </div>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    CODE REVIEW
                  </Link>
                </div>
              </div>
            </div>
          </li>
          <li>
            <div className={styles.main_section04_itemBack} ref={elem => (sec04itemBack.current[4] = elem)}></div>
            <div className={styles.main_section04_itemBox}>
              <div className={styles.main_section04_itemLeft}>
                <div className={styles.main_section04_item_num}>
                  <span className={styles.main_section04_item_num_text}>05</span>
                </div>
              </div>
              <div className={styles.main_section04_itemCenter}>
                <div className={styles.main_section04_item_title}>
                  <span className={styles.main_section04_item_title_text}>GUESS (프로모션션)</span>
                </div>
                <div className={styles.main_section04_item_ment}>
                  <span className={styles.main_section04_item_ment_text}>
                    월에 4~5건의 프로모션 랜딩페이지 퍼블리싱으로 이벤트 기간이 정해있는 경우가 많아서 TIMER나 SWIPER 등의 스크립트와 다른 요소들의 인터랙션이 CAFE24와 충돌하지않고 잘 재생되는 것에 중점을 두었습니다.
                  </span>
                </div>
                <div className={styles.main_section04_item_tag}>
                  <span className={styles.main_section04_item_tag_text}>HTML</span>
                  <span className={styles.main_section04_item_tag_text}>CSS</span>
                  <span className={styles.main_section04_item_tag_text}>JAVASCRIPT</span>
                  <span className={styles.main_section04_item_tag_text}>GSAP</span>
                  <span className={styles.main_section04_item_tag_text}>FULLPAGE.JS</span>
                  <span className={styles.main_section04_item_tag_text}>SWIPER.JS</span>
                </div>
              </div>
              <div className={styles.main_section04_itemRight}>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    GO SITE
                  </Link>
                </div>
                <div className={styles.main_section04_item_link}>
                  <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
                    CODE REVIEW
                  </Link>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Section04;
