import React, { useEffect, useRef } from 'react';
import Header from './Component/Header/Header';
import Footer from './Component/Footer/Footer';
import MouseCursor from './Hook/MouseCursor/MouseCursor';
import Section01 from './Component/Layouts/Section01/Section01';
import Section02 from './Component/Layouts/Section02/Section02';
import Section03 from './Component/Layouts/Section03/Section03';
import Section04 from './Component/Layouts/Section04/Section04';
import Section05 from './Component/Layouts/Section05/Section05';
import Lenis from '@studio-freight/lenis';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);


function App() {  
  
      
    window.addEventListener("load", function () {
      const scrollColorElems = document.querySelectorAll("[data-bgcolor]");
      scrollColorElems.forEach((colorSection, i) => {
        const prevBg = i === 0 ? "" : scrollColorElems[i - 1].dataset.bgcolor;
        const prevText = i === 0 ? "" : scrollColorElems[i - 1].dataset.textcolor;

        ScrollTrigger.create({
          trigger: colorSection,
          start: "top 50%",
          markers: true,
          onEnter: () =>
            gsap.to(".container", {
              backgroundColor: colorSection.dataset.bgcolor,
              color: colorSection.dataset.textcolor,
              overwrite: "auto"
            }),
          onLeaveBack: () =>
            gsap.to(".container", {
              backgroundColor: prevBg,
              color: prevText,
              overwrite: "auto"
            })
        });
      });
    });

  
  
  const lenis = useRef(null);

  useEffect(() => {
    // Initialize Lenis
    lenis.current = new Lenis({
      duration: 0.6, // Control the duration of the scroll
      easing: (t) => 1 - Math.pow(1 - t, 3), // Cubic easing for smooth stop
      smooth: true,
      smoothTouch: true, // Enable smooth scrolling on touch devices
    });

    const animate = (time) => {
      lenis.current.raf(time);
      requestAnimationFrame(animate);
    };

    requestAnimationFrame(animate);

    // Cleanup on unmount
    return () => {
      lenis.current.destroy();
    };

    
  }, []);
  return (
    <div className="App">
        <div className='container'>
          <MouseCursor />
          <Header />
          <Section01 />
          <Section02 />
          <Section03 />
          <Section04 />
          <Section05 />
          <Footer />
        </div>
    </div>
  );
}

export default App;
