import { gsap } from "gsap";
import React, { useLayoutEffect, useRef } from "react";
import styles from '../../../App.module.scss';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Lottie from "lottie-react";
import lottie_mouse from "../../../assets/images/lottie_mouse.json";



gsap.registerPlugin(ScrollTrigger);
function Section02() {
  const parents = useRef();
  const section02_scroll = useRef();
  
  const txtRefBox01_1 = useRef([]);
  const txtRefBox01_2 = useRef();
  const txtRef01_1 = useRef([]);
  const txtRef01_2 = useRef();
 
  useLayoutEffect(() => {
    let ctx = gsap.context(() => {
      gsap.set(section02_scroll.current, {
        yPercent: -180,
      });
      gsap.to(section02_scroll.current, {
        yPercent: 0,
        scrollTrigger: {
          trigger: parents.current,
          start: "top bottom",
          pin: false,
          scrub: true,
        }
      });
      
      txtRef01_1.current.forEach((el, index) => {
        gsap.fromTo(el, {
          autoAlpha: 0,
          yPercent: 100,
        }, {
        yPercent: 0,
        duration: 0.5, 
        autoAlpha: 1,
        ease: 'ease.in',
        scrollTrigger: {
          id: txtRefBox01_1[index],
          trigger: el,
          start: "10% bottom",
          toggleActions: 'play none none reverse',
        }
      });
    });
      
      gsap.set(txtRef01_2.current, {
        xPercent: 100,
      });
      gsap.to(txtRef01_2.current, {
        xPercent: 0,
        scrollTrigger: {
          trigger: txtRefBox01_2.current,
          start: "top bottom",
          end: "top bottom",
          toggleActions: "play none none reverse",
          pin: false,
          scrub: 2,
        }
      });


    }, parents);
    
    return () => ctx.revert();
  }, []);

  return (
    <div className={styles.main_section02} ref={parents}>
      <div className={styles.main_section02_wrap}>
        <div className={styles.main_section02_scroll} ref={section02_scroll}>
          <Lottie className={styles.section_scroll_img} animationData={lottie_mouse} loop play speed={5} /> 
        </div>
        <div className={styles.main_section02_list}>
          <div className={styles.main_section02_textbox01}>
            <div className={styles.main_text_wrap}>
              <div className={`${styles.main_text_wrap_box} ${styles.main_text_wrap_top}`}>
                <div className={`${styles.main_section02_text} ${styles.main_section02_text_top}`} ref={elem => (txtRefBox01_1.current[0] = elem)}>
                  <span ref={elem => (txtRef01_1.current[0] = elem)}>EASY TO USE UI</span>
                </div>
                <div className={`${styles.main_section02_text} ${styles.main_section02_text_top}`} ref={elem => (txtRefBox01_1.current[1] = elem)}>
                  <span ref={elem => (txtRef01_1.current[1] = elem)}>MEMORABLE UX</span>
                </div>
              </div>
              <div className={`${styles.main_text_wrap_box} ${styles.main_text_wrap_bottom}`}>
                <div className={`${styles.main_section02_text} ${styles.main_section02_text_bottom}`} ref={elem => (txtRefBox01_1.current[2] = elem)}>
                  <span ref={elem => (txtRef01_1.current[2] = elem)}>WEB STANDARDS</span>
                </div>
                <div className={`${styles.main_section02_text} ${styles.main_section02_text_bottom}`} ref={elem => (txtRefBox01_1.current[3] = elem)}>
                  <span ref={elem => (txtRef01_1.current[3] = elem)}>WEB ACCESSIBILITY</span>
                </div>
              </div>
            </div>
          </div>
          <div className={styles.main_section02_textbox02} ref={txtRefBox01_2}>
            <span ref={txtRef01_2}>
              단순히 디자인을 코드로 옮기는 것이 아닌<br />
              <strong>사용하기 쉬운 UI</strong>와 <strong>기억에 남는 UX</strong>를 고려해서 작업해요.<br />
              어떤 환경에서도 잘 보이는 <strong>반응형</strong>, <strong>웹표준과 웹접근성</strong>을 준수해서 개발합니다.<br />
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Section02;
