import { gsap } from "gsap";
import React, { useLayoutEffect, useRef } from "react";
import styles from '../../../App.module.scss';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Link } from "react-router";
import Lottie from "lottie-react";
import lottie_mouse from "../../../assets/images/lottie_mouse.json";
import ico_right from "../../../assets/images/ico_right_b.svg";

gsap.registerPlugin(ScrollTrigger);


function Section05() {

  

  const parents = useRef();
  const sec05Hypen = useRef();
  const sec05Scroll = useRef();
  const sec05Title = useRef([]);
  
  useLayoutEffect(() => {
    let ctx = gsap.context(() => {

      gsap.set(sec05Hypen.current, {
        height: 0,
      });
      gsap.to(sec05Hypen.current, {
        height: 250,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: true,
        }
      });
      gsap.set(sec05Scroll.current, {
        yPercent: 100,
        opacity: 0,
      });
      gsap.to(sec05Scroll.current, {
        yPercent: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: 2,
        }
      });


      gsap.to(sec05Scroll.current, {
        yPercent: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: 2,
        }
      });


      gsap.set(sec05Title.current, {scale: 1, y: 100, opacity: 0,});
      gsap.to(sec05Title.current, {
        scale: 1,
        opacity: 1,
        y: 0,
        duration: 1,
        ease: "bounce.out",
        stagger: 0.05,
        scrollTrigger: {
          trigger: parents.current,
          start: "-30% top",
          //end: "top bottom",
          toggleActions: "play none none reverse",
        }
      });


    }, parents);

    return () => ctx.revert();
  }, []);

  return (
    <div className={styles.main_section05} ref={parents}>
      <div className={styles.section_hypen} ref={sec05Hypen}></div>
      <div className={styles.section_scroll_wrap} ref={sec05Scroll}>
        <div className={styles.section_scroll}>
          <Lottie animationData={lottie_mouse} loop play speed={5} />
        </div>
      </div>
      <div className={styles.section_title}>
        <span ref={elem => (sec05Title.current[0] = elem)}>L</span>
        <span ref={elem => (sec05Title.current[1] = elem)}>e</span>
        <span ref={elem => (sec05Title.current[2] = elem)}>t</span>
        <span ref={elem => (sec05Title.current[3] = elem)}>`</span>
        <span className={styles.section_title_space} ref={elem => (sec05Title.current[4] = elem)}>s</span>
        <span ref={elem => (sec05Title.current[5] = elem)}>g</span>
        <span ref={elem => (sec05Title.current[6] = elem)}>e</span>
        <span className={styles.section_title_space} ref={elem => (sec05Title.current[7] = elem)}>t</span>
        <span ref={elem => (sec05Title.current[8] = elem)}>t</span>
        <span ref={elem => (sec05Title.current[9] = elem)}>o</span>
        <span ref={elem => (sec05Title.current[10] = elem)}>g</span>
        <span ref={elem => (sec05Title.current[11] = elem)}>e</span>
        <span ref={elem => (sec05Title.current[12] = elem)}>t</span>
        <span ref={elem => (sec05Title.current[13] = elem)}>h</span>
        <span ref={elem => (sec05Title.current[14] = elem)}>e</span>
        <span ref={elem => (sec05Title.current[15] = elem)}>r</span>
      </div>
      <div className={styles.main_section05_wrap}>
        <div className={styles.main_section05_text}>
          <span>
            
          </span>
        </div>
        <div className={styles.main_section05_link}>
          <div className={styles.main_section05_linkItem}>
            <Link onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
              <span>E-MAIL</span>
              <img src={ico_right} alt="오른쪽 방향 화살표" />
            </Link>
          </div>
          <div className={styles.main_section05_linkItem}>
            <Link onClick={() => window.location = 'tel:01040074018'}>
              <span>PHONE</span>
              <img src={ico_right} alt="오른쪽 방향 화살표" />
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Section05;
