import { gsap } from "gsap";
import React, { useLayoutEffect, useRef } from "react";
import styles from '../../../App.module.scss';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectFade, Autoplay } from 'swiper/modules';
import { Link } from "react-router";
import Lottie from "lottie-react";
import lottie_mouse from "../../../assets/images/lottie_mouse.json";

import about_img_01 from "../../../assets/images/about_01.jpg";
import about_img_02 from "../../../assets/images/about_02.jpg";
import about_img_03 from "../../../assets/images/about_03.jpg";
import ico_right from "../../../assets/images/ico_right_w.svg";

import 'swiper/css';
import 'swiper/css/effect-fade';

gsap.registerPlugin(ScrollTrigger);
function Section03() {
  const parents = useRef();
  const sec03Hypen = useRef();
  const sec03Scroll = useRef();
  const sec03Text = useRef([]);
  const sec03Title = useRef([]);

  const sec03Line = useRef();
  
  const sec05keyScale = useRef([]);
  const sec05keyRight = useRef([]);
  const sec05keyLeft = useRef([]);
  const sec05keyTop = useRef([]);
  const sec05keyBottom = useRef([]);
  useLayoutEffect(() => {
    let ctx = gsap.context(() => {

      gsap.set(sec03Hypen.current, {
        height: 0,
      });
      gsap.to(sec03Hypen.current, {
        height: 250,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: true,
        }
      });
      gsap.set(sec03Scroll.current, {
        yPercent: 100,
        opacity: 0,
      });
      gsap.to(sec03Scroll.current, {
        yPercent: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: parents.current,
          start: "5% 90%",
          end: "5% 90%",
          pin: false,
          scrub: 2,
        }
      });


      sec03Text.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          yPercent: 40,
        }, {
          duration: 0.8,
          opacity: 1, 
          yPercent: 0,
          ease: 'ease.in',
          stagger: 0.5,
          scrollTrigger: {
            id: sec03Text[index],
            trigger: el,
            start: "20% bottom",
            toggleActions: 'play none none reverse',
          }
        });
      });

      
      gsap.set(sec03Title.current, {scale: 1, y: 100, opacity: 0,});
      gsap.to(sec03Title.current, {
        scale: 1,
        opacity: 1,
        y: 0,
        duration: 1,
        ease: "bounce.out",
        stagger: 0.05,
        scrollTrigger: {
          trigger: parents.current,
          start: "20% bottom",
          //end: "top bottom",
          toggleActions: "play none none reverse",
        }
      });



      sec05keyScale.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          scale: 0.5,
        }, {
          opacity: 1,
          duration: 0.2, 
          scale: 1,
          ease: 'power4.inOut',
          scrollTrigger: {
            id: sec05keyScale[index],
            trigger: el,
            start: "40% bottom",
            toggleActions: 'play none none reverse'
          }
        });
      });
      
      sec05keyRight.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          xPercent: 100,
        }, {
          opacity: 1,
          duration: 0.2, 
          xPercent: 0,
          ease: 'power4.inOut',
          scrollTrigger: {
            id: sec05keyRight[index],
            trigger: el,
            start: "40% bottom",
            toggleActions: 'play none none reverse'
          }
        });
      });
      
      
      sec05keyLeft.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          xPercent: -100,
        }, {
          opacity: 1,
          duration: 0.2, 
          xPercent: 0,
          ease: 'power4.inOut',
          scrollTrigger: {
            id: sec05keyLeft[index],
            trigger: el,
            start: "40% bottom",
            toggleActions: 'play none none reverse'
          }
        });
      });
      
      
      sec05keyTop.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          yPercent: 100,
        }, {
          opacity: 1,
          duration: 0.2, 
          yPercent: 0,
          ease: 'power4.inOut',
          scrollTrigger: {
            id: sec05keyTop[index],
            trigger: el,
            start: "40% bottom",
            toggleActions: 'play none none reverse',
          }
        });
      });

      sec05keyBottom.current.forEach((el, index) => {
        gsap.fromTo(el, {
          opacity: 0,
          yPercent: -100,
        }, {
          opacity: 1,
          duration: 0.2, 
          yPercent: 0,
          ease: 'power4.inOut',
          scrollTrigger: {
            id: sec05keyBottom[index],
            trigger: el,
            start: "40% bottom",
            toggleActions: 'play none none reverse'
          }
        });
      });
      

      gsap.set("span i", {scale: 1, y: 100, opacity: 0,});
      gsap.to("span i", {
        scale: 1,
        opacity: 1,
        y: 0,
        duration: 1,
        ease: "bounce.out",
        stagger: 0.05,
        scrollTrigger: {
          trigger: parents.current,
          start: "20% bottom",
          //end: "top bottom",
          toggleActions: "play none none reverse",
          markers: true,
        }
      });
      
      gsap.set("span i", {scale: 1, y: 100, opacity: 0,});
      gsap.to("span i", {
        scale: 1,
        opacity: 1,
        y: 0,
        duration: 1,
        ease: "bounce.out",
        stagger: 0.0155,
        scrollTrigger: {
          trigger: sec03Line.current,
          start: "40% bottom",
          //end: "top bottom",
          toggleActions: "play none none reverse",
        }
      });

    }, parents);
    
    return () => ctx.revert();
  }, []);

  return (
    <div className={styles.main_section03} ref={parents}>
      <div className={styles.section_hypen} ref={sec03Hypen}></div>
      <div className={styles.section_scroll_wrap} ref={sec03Scroll}>
        <div className={styles.section_scroll}>
          <Lottie className={styles.section_scroll_img} animationData={lottie_mouse} loop play speed={5} />
        </div>
      </div>
      <div className={styles.section_title}>
        <span ref={elem => (sec03Title.current[0] = elem)}>A</span>
        <span ref={elem => (sec03Title.current[1] = elem)}>B</span>
        <span ref={elem => (sec03Title.current[2] = elem)}>O</span>
        <span ref={elem => (sec03Title.current[3] = elem)}>U</span>
        <span ref={elem => (sec03Title.current[4] = elem)}>T</span>
        <span ref={elem => (sec03Title.current[5] = elem)}>-</span>
        <span ref={elem => (sec03Title.current[6] = elem)}>M</span>
        <span ref={elem => (sec03Title.current[7] = elem)}>E</span>
      </div>
      <div className={styles.main_section03_Wrap}>
        <div className={styles.main_section03_Swiper}>
          <Swiper
            effect={'fade'}
            centeredSlides={true}
            autoplay={{
              delay: 1000,
              disableOnInteraction: false,
            }}
            modules={[Autoplay, EffectFade]}
            className={styles.main_section03_Swiper}
          >
            <SwiperSlide className={styles.main_section03_slide}>
              <img src={about_img_01}  alt="about_img1"/>
            </SwiperSlide>
            <SwiperSlide className={styles.main_section03_slide}>
              <img src={about_img_02}  alt="about_img2"/>
            </SwiperSlide>
            <SwiperSlide className={styles.main_section03_slide}>
              <img src={about_img_03}  alt="about_img3"/>
            </SwiperSlide>
          </Swiper>
        </div>
        <div className={styles.main_section03_box}>
          <div className={styles.main_section03_about}>
            <div className={styles.main_section03_aboutBox}>
              <div className={`${styles.main_section03_textbox} ${styles.main_section03_textbox01}`}>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textLeft}`} ref={elem => (sec05keyScale.current[0] = elem)}>
                  <span className={styles.main_section03_text}>#DILIGENCS</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textCenter}`}>
                  <Link></Link>
                  <span className={styles.main_section03_text}>
                    <i>#</i>  
                    <i>R</i>  
                    <i>E</i>  
                    <i>S</i>  
                    <i>P</i>  
                    <i>O</i>  
                    <i>N</i>  
                    <i>S</i>  
                    <i>I</i>
                    <i>B</i>
                    <i>I</i>
                    <i>L</i>
                    <i>I</i>
                    <i>T</i>
                    <i>Y</i>
                  </span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textRight}`} ref={elem => (sec05keyRight.current[0] = elem)}>
                  <Link></Link>
                  <span className={styles.main_section03_text}>#METICULOUS</span>
                </div>
              </div>
              <div className={`${styles.main_section03_textbox} ${styles.main_section03_textbox02}`}>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textLeft}`} ref={elem => (sec05keyScale.current[1] = elem)}>
                  <Link>
                    <span className={styles.main_section03_text}>
                      <i>#</i>  
                      <i>W</i>  
                      <i>E</i>  
                      <i>L</i>  
                      <i>L</i>  
                      <i>-</i>  
                      <i>M</i>  
                      <i>E</i>  
                      <i>L</i>
                      <i>T</i>
                    </span>
                  </Link>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textCenter}`} ref={elem => (sec05keyTop.current[0] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#DEVELOPMENTAL</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textRight}`} ref={elem => (sec05keyBottom.current[0] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#POSITIVE</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textLeft}`}  ref={elem => (sec05keyScale.current[2] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#HTML</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textCenter}`} ref={elem => (sec05keyLeft.current[0] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#CSS</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textCenter}`}  ref={elem => (sec05keyRight.current[1] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#SCSS</span>
                </div>
              </div>
              <div className={`${styles.main_section03_textbox} ${styles.main_section03_textbox03}`}>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textRight}`}>
                <Link></Link>
                  <span className={styles.main_section03_text}>
                    <i>#</i>
                    <i>J</i>
                    <i>A</i>
                    <i>V</i>
                    <i>A</i>
                    <i>S</i>
                    <i>C</i>
                    <i>R</i>
                    <i>I</i>
                    <i>P</i>
                    <i>T</i>
                  </span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textLeft}`} ref={elem => (sec05keyTop.current[1] = elem)}>
                <Link></Link>
                  <span className={styles.main_section03_text}>#JQUERY</span>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textCenter}`} ref={elem => (sec05keyLeft.current[1] = elem)}>
                  <Link>
                    <span className={styles.main_section03_text}>#GIT</span>
                  </Link>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textRight}`} ref={elem => (sec05keyScale.current[3] = elem)}>
                  <Link>
                    <span className={styles.main_section03_text}>#FIGMA</span>
                  </Link>
                </div>
                <div className={`${styles.main_section03_text} ${styles.main_section03_textLeft}`} ref={elem => (sec05keyRight.current[2] = elem)}>
                  <Link>
                    <span className={styles.main_section03_text}>#ZEPLIN</span>
                  </Link>
                </div>
              </div>
            </div>
            <Link className={styles.mail_link} onClick={() => window.location = 'mailto:sanghow3091@gmail.com'}>
              <div>
                <span>
                  MOREVIEW
                </span>
                <img src={ico_right} alt="오른쪽 방향 화살표" />
              </div>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Section03;
